import React from 'react'
import { render } from 'react-dom'
import { createStore } from 'redux'
import { Provider } from 'react-redux'

import { Map } from 'immutable'

import { Header } from './header'
import { Gallery } from './gallery'
import { MenuButton } from './menubutton'
import { Scrollable } from './scrollable'

// TDOD: move all "window management" out of Scrollable, remove scroll, pageHeight, etc... from state
const initialState = Map(
  {
    scroll: 0,
    headerIsVisible: true,
    pageHeight: window.innerHeight,
    scrollBarWidth: 0
  })

const reducer = function (state = initialState, action) {
  switch (action.type) {
    case 'SCROLL':
      return state.merge({
        'scroll': action.value,
        'headerIsVisible': action.value <= state.get('pageHeight')
      })
    case 'SCROLLBAR_WIDTH_UPDATED':
      return state.set('scrollBarWidth', action.value)
    case 'PAGE_HEIGHT_UPDATED':
      return state.merge({
        'pageHeight': action.value,
        'headerIsVisible': state.get('scroll') <= action.value
      })
    default:
      return state
  }
}

const store = createStore(reducer)

const Aux = props => props.children

const Root = () => {
  return (
    <Provider store={store}>
      <Aux>
        <MenuButton/>
        <Scrollable>
          <Header/>
          <Gallery images={[
            { url: '/img/tile_1.jpg', shape: 'is-4by3' },
            { url: '/img/tile_2.jpg', shape: 'is-3by4' },
            { url: '/img/tile_3.jpg', shape: 'is-3by4' }
          ]} columns={window.innerWidth < 769 ? 1 : 2}/>
        </Scrollable>
      </Aux>
    </Provider>
  )
}

render(<Root/>, document.getElementById('root'))
