import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'

const _MenuButton = (props) => (
  <div className="menu-button-container z-index-foreground" style={{ right: props.scrollBarWidth }}>
    <a href="#" className={'menu-button fas fa-bars' + (props.headerIsVisible ? ' menu-button-is-hidden' : '')}></a>
  </div>
)

_MenuButton.propTypes = {
  headerIsVisible: PropTypes.bool,
  scrollBarWidth: PropTypes.number
}

const stateToMenuButtonProps = (state) => (
  {
    headerIsVisible: state.get('headerIsVisible'),
    scrollBarWidth: state.get('scrollBarWidth')
  })

export const MenuButton = connect(stateToMenuButtonProps)(_MenuButton)
