import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'

class _Scrollable extends React.Component {
  constructor (props) {
    super(props)
    this.ref = React.createRef()
  }

  render () {
    return (
      <div
        className="enable-parallax scrollable"
        ref={this.ref}
        onScroll={() => (this.props.scroll(this.ref.current.scrollTop))}
      >
        {this.props.children}
      </div>
    )
  }

  componentDidMount () {
    this.props.scrollbarWidthUpdated(this.ref.current.offsetWidth - this.ref.current.clientWidth)
    window.addEventListener('resize', () => (this.props.pageHeightUpdated(window.innerHeight)))
  }
}

_Scrollable.propTypes = {
  headerIsVisible: PropTypes.bool,
  scroll: PropTypes.func,
  scrollbarWidthUpdated: PropTypes.func,
  pageHeightUpdated: PropTypes.func,
  children: PropTypes.arrayOf(PropTypes.node)
}

const dispatchToScrollableProps = (dispatch) => (
  {
    scroll: (value) => dispatch({ type: 'SCROLL', value: value }),
    scrollbarWidthUpdated: (value) => dispatch({ type: 'SCROLLBAR_WIDTH_UPDATED', value: value }),
    pageHeightUpdated: (value) => dispatch({ type: 'PAGE_HEIGHT_UPDATED', value: value })
  })

export const Scrollable = connect(null, dispatchToScrollableProps)(_Scrollable)
