import React from 'react'
import { connect } from 'react-redux'
import { MenuBar } from './menubar'

class _Header extends React.Component {
  render () {
    return (
      <div className="header parallax-container">
        <MenuBar/>
        <div className="parallax-layer is-parallax-layer-0">
          <h1>Maria<br/>Morozova</h1>
          <p>Illustrator</p>
        </div>
        {
          [1, 2, 3, 4, 5, 6].map(function (elem) {
            var className = 'parallax-layer is-parallax-layer-' + elem + ' is-header-layer-' + elem
            return <div className={className} key={elem}> </div>
          })
        }
      </div>)
  }
}

const stateToHeaderProps = (state) => (
  {
    headerIsVisible: state.get('headerIsVisible'),
    pageHeight: state.get('pageHeight')
  })

export const Header = connect(stateToHeaderProps)(_Header)
