import React from 'react'
import PropTypes from 'prop-types'

const Image = (props) => (
  <div className={'image ' + props.shape}>
    <img src={props.url}/>
  </div>)

Image.propTypes = {
  url: PropTypes.string,
  shape: PropTypes.string
}

export const Gallery = (props) => {
  var imagesPerColumn = Array(props.columns).fill().map(element => [])
  props.images.forEach((element, idx) => {
    imagesPerColumn[idx % props.columns].push(element)
  })

  return (
    <div className="gallery-section z-index-foreground">
      <div className="container">
        {imagesPerColumn.map((column, idx) => (
          <div className="column" key={idx}>
            {
              column.map((img, imgIdx) =>
                (
                  <div className='image-box' key={imgIdx}>
                    <Image url={img.url} shape={img.shape}/>
                  </div>
                )
              )
            }
          </div>
        ))}
      </div>
    </div>)
}

Gallery.propTypes = {
  images: PropTypes.arrayOf(PropTypes.object),
  columns: PropTypes.number
}
